import React, { useState, useEffect, Fragment } from 'react';
import './App.css';
import DayCard from './DayCard';
import BeachSelect from './BeachSelect';
import refresh from './refresh-32.png';

export default function App() {
  const [beaches, setBeaches] = useState([]);
  const [selectedBeach, setSelectedBeach] = useState('99660');
  const [isLoading, setIsLoading] = useState(true);

  const handleChange = e => {
    setSelectedBeach(e.target.value);
  };

  useEffect(() => {
    const getData = async () => {
      const result = await fetch(
        'https://cors-anywhere.herokuapp.com/https://yarraandbay.vic.gov.au/resources/br_sitereportget.json',
        { cache: 'no-store' },
      );
      const value = await result.json();

      const beachData = value.siteList;
      const beachDataSorted = [...beachData].sort((a, b) =>
        a.siteName.localeCompare(b.siteName),
      );
      setBeaches(beachDataSorted);
      setIsLoading(false);
    };

    getData();
  }, [isLoading]);

  const info = beaches.find(beach => beach.siteId === +selectedBeach) || {};

  return (
    <div className="App">
      <div className="header-banner">
        <div className="header-content">
          <header>
            <h1>Beach Report</h1>
            <img
            className={isLoading ? "rotate refresh-icon" : "refresh-icon"}
            src={refresh}
            alt="refresh"
            onClick={e => setIsLoading(true)}
          />
          </header>
          
        </div>
      </div>
      <section className="beach-content">
        {isLoading ? (
          <h2>...Loading</h2>
        ) : (
          <Fragment>
            <BeachSelect
              handleChange={handleChange}
              selectedBeach={selectedBeach}
              beaches={beaches}
            />
            <p>{info.councilName}</p>
            <div className="beach-card">
              <DayCard
                day="Today"
                updated={info.todayUpdated}
                rating={info.todayRating}
                reason={info.todayRatingReason}
              />
              <DayCard
                day="Tomorrow"
                updated={info.tomorrowUpdated}
                rating={info.tomorrowRating}
                reason={info.tomorrowRatingReason}
              />
            </div>
          </Fragment>
        )}
      </section>
    </div>
  );
}
