import React from 'react';

export default function BeachSelect({ selectedBeach, handleChange, beaches }) {

  return (
    <div className="select-container">
          <select
            id="beach-select"
            value={selectedBeach}
            onChange={handleChange}
          >
            {beaches.map(beach => {
              return (
                <option value={beach.siteId} key={beach.siteId}>
                  {beach.siteName}
                </option>
              );
            })}
          </select>
        </div>
  )
}