import React, { Fragment } from 'react';

export default function DayCard({ day, updated, rating, reason }) {
  const formatDate = dateInMs => {
    const date = new Date(dateInMs);
    const dateOptions = {
      month: 'short',
      day: 'numeric',
      year: 'numeric',
      hour: 'numeric',
      minute: 'numeric',
      timeZoneName: 'short',
    };
    return date.toLocaleDateString('au-EN', dateOptions);
  };

  const calcClass = rating => {
    if (rating === 'Good') {
      return 'good';
    } else if (rating === 'Fair') {
      return 'fair';
    } else if (rating === 'Poor') {
      return 'poor';
    } else {
      return 'na';
    }
  };

  const ratingClass = calcClass(rating);

  return (
    <Fragment>
      {!rating ? (
        <h3>{day} - Forecast data unavailable</h3>
      ) : (
        <div>
          <h3>
            {day} - <span className={ratingClass}> {rating}</span>
          </h3>
          <p>{reason}</p>
          <p className="updated-at-text">Updated: {formatDate(updated)}</p>
        </div>
      )}
    </Fragment>
  );
}
